<template>
  <div id="app">
      <div class="background">
          <div class="listPlay"  v-bind:class="{active:isModal}">
                <div class="title">
                  <div class="menu">
                  <svg fill="white" @click="openModal" version="1.1" width="30px" height="30px" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                  viewBox="0 0 469.333 469.333" style="enable-background:new 0 0 469.333 469.333;" xml:space="preserve">
                <g>
                  <g>
                    <g>
                      <path d="M53.333,106.667H416c29.417,0,53.333-23.927,53.333-53.333S445.417,0,416,0H53.333C23.917,0,0,23.927,0,53.333
                        S23.917,106.667,53.333,106.667z"/>
                      <path d="M416,181.333H53.333C23.917,181.333,0,205.26,0,234.667S23.917,288,53.333,288H416c29.417,0,53.333-23.927,53.333-53.333
                        S445.417,181.333,416,181.333z"/>
                      <path d="M416,362.667H53.333C23.917,362.667,0,386.594,0,416s23.917,53.333,53.333,53.333H416
                        c29.417,0,53.333-23.927,53.333-53.333S445.417,362.667,416,362.667z"/>
                    </g></g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g>
                <g></g><g></g>
                </svg>
                </div>
                <h1>List Music</h1>
              </div>

              <!-- List Item -->
              <ul class="listPlay__list">
                <li class="listPlay__item" v-for="(song,index) in songs" v-bind:key="index"
                  @click="play(song,index)"
                  :class="(song.src == current.src) ? 'song playing' : 'song'"
                >
                  <div class="logo">
                      <img v-bind:src="song.img">
                  </div>
                  <p class="name-music">
                    {{song.title}} -{{song.artist}}
                  </p>
                  <li/>
              </ul>
              <!-- End List Item -->
              <!-- Modal Upload Audio -->
              <div class="btn-modal">
                <button @click="openModalUpload">UpLoad Audio</button>
              </div>
              <!-- End Modal Upload Audio -->
          </div>
          <section class="player">
            <div class="img">
              <img v-bind:src="currentImage" alt="">
            </div>
            <h2 class="song-title">{{current.title}} - <span>{{current.artist}}</span></h2>
            <div class="wrapper-progress">
              <div @mousedown="onClickSetValue" class="progressBar">
                <div class="progress"></div>
              </div>	
            </div>
            <p class="time"><span>00:00 / 00:00</span></p>
            <div class="controls">
              <button class="prev"  @click="prev">Prev</button>
              <button class="play"  @click="play" v-if="!isPlaying" >Play</button>
              <button class="pause" @click="pause" v-else >Pause</button>
              <button class="next"  @click ="next">Next</button>
            </div>
          </section>
          <div class="modal" v-bind:class="{modalAnimation:isModalUpload}">
            <h1>Upload Audio</h1>
            <div class="input__upload-wrapper">
              <input type="file" @change="preveUploadAudio"  ref="onClickUpLoadAudio" >
              <input type="file" @change="preveUploadImage"    ref="onClickUpLoadImg" >
              <div class="btn-upload">
              </div>
              <input v-model="nameAudio" type="text" placeholder="Name Audio">
              <input v-model="artistAudio" type="text" placeholder="Artist Audio">
                <p v-if="valueUpload <100">Progess: {{valueUpload.toFixed() +"%"}}
                  <progress  :value="valueUpload" max="100"> </progress>
                </p>
              <div class="btn-upload__database">
                <button class="upload" @click="uploadAudio">Upload</button>
              </div>
            </div>
            <div class="modal-close" @click="closeModalUpload">
                <i class="fa fa-times-circle"></i>
            </div>
          </div>
          
          <div class="menu-bacground" v-bind:class="{click:isModal}">
                <svg @click="openModal" version="1.1" width="30px" height="30px" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
                viewBox="0 0 469.333 469.333" style="enable-background:new 0 0 469.333 469.333;" xml:space="preserve">
              <g>
                <g>
                  <g>
                    <path d="M53.333,106.667H416c29.417,0,53.333-23.927,53.333-53.333S445.417,0,416,0H53.333C23.917,0,0,23.927,0,53.333
                      S23.917,106.667,53.333,106.667z"/>
                    <path d="M416,181.333H53.333C23.917,181.333,0,205.26,0,234.667S23.917,288,53.333,288H416c29.417,0,53.333-23.927,53.333-53.333
                      S445.417,181.333,416,181.333z"/>
                    <path d="M416,362.667H53.333C23.917,362.667,0,386.594,0,416s23.917,53.333,53.333,53.333H416
                      c29.417,0,53.333-23.927,53.333-53.333S445.417,362.667,416,362.667z"/>
                  </g></g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g>
              <g></g><g></g>
              </svg>
              </div>
      
      
      
      
      </div>
  </div>
</template>

<script>
import firebase from './firebase'
export default {
  name: 'App',
  data() {
    return {
      isModal:false,
      current:{},
      index:0,
      isPlaying:false,
      currentSeconds:0,
      currentMinutes:0,
      totalSeconds:0,
      totalMinutes:0,
      time:'',
      totalTime:'',
      isModalUpload:false,
      currnetAudioURL:null,
      AudioData:null,
      valueUpload:0,
      nameAudio:'',
      artistAudio:'',
      currnetImageURL:null,
      imageData:null,
      songs:[],
      player:new Audio()
    }
  },
  computed: {
    currentImage(){
      if(!this.current.length){
        return this.current.img
      }
      else{
        return 'https://scontent.fhan5-2.fna.fbcdn.net/v/t1.0-9/91465231_2593697780952865_6347380301009780736_o.jpg?_nc_cat=110&_nc_sid=a4a2d7&_nc_ohc=mgO3kE87pEYAX-CtRlM&_nc_ht=scontent.fhan5-2.fna&oh=f4eea4fa8655808362fa04a36df173ef&oe=5ED5EBDE'
      }
    }
  },
  methods: {
    uploadAudio(){
      this.currnetAudioURL= null
      const storageRef = firebase.storage().ref(`audio/${this.AudioData.name}`).put(this.AudioData)
          storageRef.on('state_changed',snapshot =>{
        this.valueUpload=(snapshot.bytesTransferred/snapshot.totalBytes)*100
      },null,
        () =>{
          this.valueUpload=90
          storageRef.snapshot.ref.getDownloadURL()
          .then(url =>{
            this.currnetAudioURL=url
          })
          .then(() =>{
              this.currnetImageURL=null 
              const imageRef = firebase.storage().ref(`image/${this.imageData.name}`).put(this.imageData)
                  imageRef.on('state_changed', snapshot =>{
                    this.valueUpload=(snapshot.bytesTransferred/snapshot.totalBytes)*100
                },null,
                  () =>{
                    this.valueUpload=100
                    imageRef.snapshot.ref.getDownloadURL()
                    .then(urlImage =>{
                      this.currnetImageURL=urlImage
                      firebase.database().ref('users')
                        .push()
                        .set({
                          title:this.nameAudio,
                          artist:this.artistAudio,
                          src:this.currnetAudioURL,
                          img:this.currnetImageURL
                        })
                        .then(() =>{
                          this.nameAudio='',
                          this.artistAudio='',
                          this.currnetImageURL=null,
                          this.currnetAudioURL=null
                          this.closeModalUpload()
                          this.getData()
                        })
                    })
                  }
                )
            })
          }
        )
      },
    preveUploadAudio(event){
      this.currnetAudioURL= null
      this.AudioData = event.target.files[0]
      console.log(this.AudioData);
      
    },
    getData(){
      firebase.database()
        .ref('users')
        .once('value')
        .then((snap) =>{
            this.songs=[]
            snap.forEach((item) =>{
                this.songs.push({...item.val()})
            })
            
        })
    },
    preveUploadImage(event){
      this.currnetImageURL= null
      this.imageData = event.target.files[0]
    },
    openModal(){
      this.isModal=!this.isModal
    },
    onClickSetValue(event){
        var width = document.querySelector('.progressBar').clientWidth;
        var clickposition = event.clientX - event.target.offsetLeft;
        this.player.currentTime = (clickposition /width) * this.player.duration
    },
    smartTime(time){
      return time < 10 ? "0" + time.toString().trim() : time
    },
    onChangeValue(){
      var progress = document.querySelector('.progress')
      var progressBar = document.querySelector('.progressBar')
      var time = document.querySelector('.time')
      progress.style.width = ((this.player.currentTime / this.player.duration) * 100) +"%";
      this.currentSeconds = this.smartTime(Math.floor(this.player.currentTime %60))
      this.currentMinutes = this.smartTime( Math.floor(this.player.currentTime /60))
      this.totalSeconds   = this.smartTime(Math.floor(this.player.duration % 60))
      this.totalMinutes   = this.smartTime(Math.floor(this.player.duration/60))
      this.time = this.currentMinutes + ':' + this.currentSeconds
      this.totalTime = this.totalMinutes + ':' + this.totalSeconds
      if(this.time !== '' && this.totalTime !== ''){
        time.innerHTML = this.time + ' / ' + this.totalTime
      }
      if(Math.floor(this.player.currentTime) === Math.floor(this.player.duration)){
        this.index++;
        if (this.index > this.songs.length - 1) {
            this.index = 0;
        }
        this.current = this.songs[this.index];
        if(this.current){
          this.play(this.current,this.index);
        }
      }
    },
    play(song,index){
      if(typeof this.current.src  === "undefined"){
        this.current = this.songs[0]
        this.player.src = this.current.src
      }
      
      if(typeof song.src != "undefined"){
        this.current =song
        this.index=index
        this.player.src = this.current.src
      }
      setInterval(this.onChangeValue,1000)
      this.player.play();
      this.isPlaying=true
    },
    pause(){
      this.player.pause()
      this.isPlaying=false
    },
    next(){
      this.index++
      if(this.index > this.songs.length -1){
        this.index=0
      }
      this.current = this.songs[this.index]
      this.play(this.current ,this.index)
    },
    prev(){
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }
      this.current = this.songs[this.index];
      this.play(this.current ,this.index)
    },
    openModalUpload(){
      this.isModalUpload=true
    },
    closeModalUpload(){
      this.isModalUpload=false
    }
  },
  created() {
    this.getData()
  
  }
}
</script>

<style>

