}
/*  Progress*/
.time{
  display: inline-block;
  margin-top: 20px;
  color: white;
}
#seek-bar{
  position: relative;
}
#progebar{
  width: 100%;
  height: 10px;
  border-radius: 5px;
  outline: none;
  -webkit-appearance:none;
  position: relative;
  z-index: 2;
