  object-fit: cover;
}
@keyframes snip {
    from{
        -webkit-transform:rotate(0deg)  ;
        -moz-transform:rotate(0deg) ;
        -o-transform:rotate(0deg) ;
    }
    to{
        -webkit-transform:rotate(360deg) ;
        -moz-transform:rotate(360deg) ;
        -o-transform:rotate(360deg) ;

    }
}
.song-title {
  color: #ffffff;
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.song-title span {
  font-weight: 400;
  font-style: italic;
}
/* Control */
.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 15px;
}

